@charset "UTF-8";
.Comment {
  /*
     * El selector de hijo, es necesario para aumentar la especifidad
     */ }
  .Comment .header {
    padding: 10px; }
    .Comment .header h3 {
      color: #30697d; }
  .Comment .start {
    padding: 10px;
    border-top: 1px #bdc3c7 solid;
    border-bottom: 1px #bdc3c7 solid; }
    .Comment .start .vote {
      color: #e79423;
      margin: 0;
      padding: 0; }
      .Comment .start .vote span {
        font-size: 0.6em; }
  .Comment form {
    margin-top: 12px;
    padding: 10px;
    text-align: center; }
    .Comment form label {
      text-transform: uppercase;
      font-weight: bold;
      float: left; }
    .Comment form textarea {
      border: 1px #bdc3c7 solid; }
    .Comment form button {
      background-color: #f1cb2f;
      color: #000;
      text-transform: uppercase;
      font-weight: bold;
      border: none;
      padding: 8px 44px 8px 44px;
      margin-top: 24px; }
  .Comment .ec-stars-wrapper {
    /* Espacio entre los inline-block (los hijos, los `a`)
    	   http://ksesocss.blogspot.com/2012/03/display-inline-block-y-sus-empeno-en.html */
    font-size: 0;
    /* Podríamos quitarlo,
    		pero de esta manera (siempre que no le demos padding),
    		sólo aplicará la regla .ec-stars-wrapper:hover a cuando
    		también se esté haciendo hover a alguna estrella */
    display: inline-block;
    width: 100%;
    padding: 12px;
    text-align: center; }
  .Comment .ec-stars-wrapper a {
    color: #e79423 !important;
    padding: 4px;
    text-decoration: none;
    display: inline-block;
    /* Volver a dar tamaño al texto */
    font-size: 42px;
    font-size: 3rem;
    color: #888; }
  .Comment .ec-stars-wrapper:hover a {
    color: #2782e4; }
  .Comment .ec-stars-wrapper > a:hover ~ a {
    color: #888; }
